

import React, { useState } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import { useDrag, usePinch } from '@use-gesture/react';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const PDFViewerWithGesture = ({ filePath }) => {
    const [scale, setScale] = useState(1);  // 初始缩放比例
    const [numPages, setNumPages] = useState(null);  // PDF的页数
    const [position, setPosition] = useState([0, 0]);  // 用于拖动页面的位置

    // Pinch手势：用于缩放
    const bindPinch = usePinch((state) => {
        setScale(state.offset[0]);  // 更新缩放比例
    });

    // Drag手势：用于拖动页面
    const bindDrag = useDrag((state) => {
        setPosition([state.offset[0], state.offset[1]]);  // 更新拖动的位置
    });

    // PDF加载完成后，获取页面数
    const onLoadSuccess = ({ numPages }) => {
        setNumPages(numPages);
    };

    return (
        <div
            {...bindPinch()}  // 将 pinch 手势绑定到容器
            {...bindDrag()}   // 将 drag 手势绑定到容器
            style={{
                width: '100%',
                height: '100%',
                // overflow: 'hidden',
                cursor: 'grab',
                transform: `translate(${position[0]}px, ${position[1]}px)`,  // 设置拖动的位移
                touchAction: 'auto',  // 禁止默认的触摸事件行为
            }}
        >
            <Document file={filePath} onLoadSuccess={onLoadSuccess}>
                   {new Array(numPages).fill("").map((item, index) =>
                              <Page
                                key={index}
                                pageNumber={index + 1}
                                scale={scale}
                                width={window.innerWidth}
                                renderAnnotationLayer={false}
                                renderTextLayer={false}
                        />)}
            </Document>
        </div>
    );
};

export default PDFViewerWithGesture;
